/*

Buttons

*/
.btn {
  position: relative;
  /*координаты?*/
  display: inline-block;
  outline: none;
  text-decoration: none;
  text-align: center;
  font-family: "Corbel", sans-serif;
  /*corbel*/
  font-size: 18px;
  font-weight: 400;
  line-height: 1;
  padding: 13px 20px;
  /**/
  white-space: nowrap;
  border-radius: 0px;
  /*check*/
  height: 50px;
  /*check*/
  min-width: 240px;
  /*check*/
  color: #ffffff !important;
  -webkit-appearance: none;
  background: transparent;
  /*$color4;*/
  /*переназначить?*/
  user-select: none;
  backface-visibility: hidden;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  transition: all 0.3s;
  cursor: pointer !important;
  border: 1px solid #ffffff;
}

.btn:before {
  content: '';
  position: absolute;
  left: -1px;
  top: 50%;
  transform: translateY(-7px);
  border: 7px solid transparent;
  border-left: 7px solid #656e4d;
}

.btn:hover {
  border: 2px solid #656e4d;
  background: #656e4d !important;
}

.btn::-moz-focus-inner {
  padding: 0 !important;
  border: 0 none !important;
}

.btn:active {
  text-decoration: none;
}

.btn[disabled], .btn.disabled {
  background-color: #ebebeb;
  color: #888 !important;
  border-color: inherit;
  border-color: currentColor;
  opacity: 1;
  cursor: default;
  cursor: not-allowed;
}

.btn > input[type='file'] {
  opacity: 0;
  filter: alpha(opacity=0);
  font-size: 120px;
  position: absolute;
  top: -20px;
  right: -10px;
  vertical-align: top;
  z-index: 3;
  cursor: pointer;
}

.btn--wide {
  min-width: 300px;
}

.btn--white {
  border: 1px solid #e4ddd4;
  background: #ffffff !important;
  color: #082319 !important;
  z-index: 2;
}

.btn--white:hover {
  border: 1px solid #aab394;
  background: #aab394 !important;
  color: #ffffff !important;
}

.btn--arr:after {
  content: '\e808';
  display: inline-block;
  vertical-align: middle;
  margin-left: 8px;
  position: relative;
  color: #b4b4b4;
  font-size: 9px;
  font-family: fontello;
  transition: 0.3s;
}

.btn--arr:hover:after {
  color: #ffffff;
  transform: translateX(3px);
}

.btn--no-hover:hover {
  color: inherit !important;
  background: inherit !important;
  border-color: inherit;
  border-color: currentColor;
}

.btn--fullwidth {
  width: 100%;
  margin-top: 20px;
}

.btn--fullwidth:first-child {
  margin-top: 0;
}

