.confidence-home--filter .filter .filter_title input[type='checkbox'] {
  --active: #fff;
  --active-inner: #67544b;
  --border: #fff;
  --background: #fff;
  --disabled: #fff;
  --disabled-inner: #E1E6F9;
  -webkit-appearance: none;
  -moz-appearance: none;
  height: 21px;
  outline: none;
  display: inline-block;
  vertical-align: top;
  position: relative;
  margin: 0;
  cursor: pointer;
  border: 1px solid var(--bc, var(--border));
  background: #e4ddd4;
  transition: background .3s, border-color .3s, box-shadow .2s;
}

.confidence-home--filter .filter .filter_title input[type='checkbox']:after {
  content: '';
  display: block;
  left: 0;
  top: 0;
  position: absolute;
  transition: all 0.1s;
}

.confidence-home--filter .filter .filter_title input[type='checkbox']:checked {
  --b: var(--active);
  --bc: var(--active);
  --d-o: .3s;
  --d-t: .6s;
  --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
}

.confidence-home--filter .filter .filter_title input[type='checkbox']:not(.switch) {
  width: 21px;
}

.confidence-home--filter .filter .filter_title input[type='checkbox']:not(.switch):after {
  opacity: var(--o, 0);
}

.confidence-home--filter .filter .filter_title input[type='checkbox']:not(.switch):checked {
  --o: 1;
}

.confidence-home--filter .filter .filter_title input[type='checkbox'] + label {
  font-size: 18px;
  color: #082319;
  font-weight: 300;
  line-height: 21px;
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
  margin-left: 4px;
}

.confidence-home--filter .filter .filter_title input[type='checkbox']:not(.switch):after {
  width: 5px;
  height: 9px;
  border: 2px solid var(--active-inner);
  border-top: 0;
  border-left: 0;
